    //引入插件
	import uniCard from "@/components/uni-card/uni-card.vue"
    //引入增加评论和查找所有评论的api
	import {commentAdd,commnetQuery} from '../../static/utils/api.js';
	
	//引入登录的模块
	const login = require('../../static/utils/utils').Login;
	const query = require('../../static/utils/utils').Query;
	const Login = new login();
	const Query = new query();
	
	var _this;
	export default {
		components: {uniCard},
		data() {
			return {
				width:'',           //输入框的长度
				
				userInfo:"",        //用户的信息
				projectId:"",        //项目的id，拿到用户所在的项目的id
				sprintId:"",         //用户选中的冲刺的id
				allComment:[],      //所有的评论,包括评论的人（真实姓名），评论的内容，评论的时间
				content:"",
			}
		},
		
		onShow() {
	      this.getSystem();    
		  _this = this
		  wx.getStorage({
		  	key:"userInfo",
			success:(res)=>{
			   let id = {
				   id:res.data.id
			   }
			   Query.findUser(id)
			   .then(data=>{
				   console.log("查询到的用户的信息",data.data.records[0])
				   _this.userInfo = data.data.records[0];
				   uni.getStorage({
					   key:"nowInProject",
					   success:(res)=>{
						   _this.projectId = res.data.projectId;
						   uni.getStorage({
							   key:'sprintId',
							   success:(res)=>{
								   _this.sprintId = res.data;
								   _this.queryComment();
							   }
						   })
					   }
				   })
			   })
			   .catch(Error=>{
				   uni.showToast({
				   	 title: '网络错误',
					 duration:500,
					 icon:"none"
				   });
			   })
			},
			fail:()=>{
			  uni.redirectTo({
			  	url:'../login/login'
			  })
			}
		  })
		},
		
		
		methods: {
			
			//获取系统信息设置输入框长度
			getSystem:function(){
				_this = this;
				uni.getSystemInfo({
					success:(res)=>{
						_this.width = (parseInt(res.windowWidth-80));
					}
				})
			},
			
			//输入评论的内容
			inputContent:function(e){
				let content = e.detail.value;
				if(content.length<=30){
					this.content = content
				}else{
					uni.showToast({
						title:"内容过长",
						duration:1000,
						icon:"none"
					})
				}
			},
			
		   //增加评论
		   addComment:function(){
				_this  = this;
				if(_this.content){
					uni.showLoading({
						title:'增加中',
						success:()=>{
							uni.request({
								url:commentAdd,
								method:"POST",
								data:{
									userId:_this.userInfo.id,
									projectId:_this.projectId,
									sprintId:_this.sprintId,
									content:_this.content
								},
								dataType:'json'
							})
							.then(data=>{  
								console.log("添加评论成功",data)
								uni.hideLoading();
								_this.queryComment();
							})
							.catch(Error=>{
								uni.showToast({
									title:"增加评论失败",
									icon:"none",
									duration:500
								})
							})	
						}
					})
				 }else{
					 uni.showToast({
						title:"评论有误",
						duration:1000,
						icon:"none"
					 })
				 }
		   },
		 
		  //查找评论	 
		  queryComment:function(){
			
			 _this = this; 
			 uni.showLoading({
			 	title:"获取中",
				success:()=>{
					 uni.request({
						url:commnetQuery,
						data:{ 
							projectId: _this.projectId,
							sprintId :_this.sprintId
						},
						method:'POST',
						dataType:'json'
					})
					.then(data=>{  
						uni.hideLoading();
						console.log(data)
						console.log("获取到的评论",data[1].data.data.records)
					    _this.allComment =  data[1].data.data.records;
					})
					.catch(Error=>{
						 uni.showToast({
							icon:"none",
							title:"网络错误",  
							duration:1000
						 })
					})
				}
			 })
		  },
	},
}
